body {
  background-attachment: fixed;
  background-color: black;
  background-image: url('https://cdn.pixabay.com/photo/2018/08/15/13/10/galaxy-3608029_960_720.jpg');
  background-position: center;
  background-size: cover;
  color: whitesmoke;
  margin: 0;
}

header {
  background-color: white;
  display: flex;
  height: 80px;
  justify-content: center;
  opacity: 0.5;
  text-align: center;
  text-transform: uppercase;
}

header h1 {
  color: black;
  opacity: 1;
}

.system-container {
  display: flex;
  flex-flow: column nowrap;
  text-shadow: 2px 2px 2px black;
}

.system-container h2 {
  font-family: Epilogue , serif;
  font-size: 40px;
  font-style: normal;
  font-weight: 300;
  line-height: 150%;
  text-align: center;
  text-shadow: 2px 2px 2px black;
  text-transform: uppercase;
}

.planets-list {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  height: 250px;
  justify-content: space-around;
  max-width: 100%;
}

.planet-card {
  align-items: center;
  display: flex;
  flex-direction: column;
  max-width: 12.5%;
}

.planet-card:hover {
  align-items: center;
  display: flex;
  flex-direction: column;
  min-width: 12.5%;
}

.planet-card img {
  max-width: 100%;
}

.planet-card:nth-child( 1 ) {
  max-width: 5%;
}

.planet-card:nth-child( 2 ) {
  max-width: 8%;
}

.planet-card:nth-child( 3 ) {
  max-width: 8%;
}

.planet-card:nth-child( 4 ) {
  max-width: 7%;
}

.planet-card:nth-child( 5 ) {
  max-width: 20%;
}

.planet-card:nth-child( 6 ) {
  max-width: 18%;
}

.planet-card:nth-child( 7 ) {
  max-width: 10%;
}

.planet-card:nth-child( 8 ) {
  max-width: 10%;
}

.planet-card:nth-child( 5 ):hover {
  max-width: 22%;
}

.planet-card:nth-child( 6 ):hover {
  max-width: 20%;
}

.missions-container {
  align-items: center;
  background-color: rgba(245, 245, 245, 0.377);
  border-radius: 30px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: 0 10px;
}

.missions-container h2 {
  flex-basis: 100%;
  flex-grow: 1;
  font-family: Epilogue , serif;
  font-size: 40px;
  font-style: normal;
  font-weight: 300;
  line-height: 150%;
  text-align: center;
  text-shadow: 2px 2px 2px black;
  text-transform: uppercase;
}

.mission-card {
  background-color: rgba(0, 0, 0, 0.8);
  border: 1px solid white;
  box-shadow: 2px 2px 2px rgba(255, 255, 255, 0.459);
  color: white;
  display: flex;
  flex-wrap: wrap;
  margin: 15px;
  max-width: 30%;
  min-width: 30%;
  opacity: 1;
}

.missions-container p {
  padding: 0 5px;
}

.missions-container p:hover {
  text-decoration: underline;
  text-decoration-color: blue;
}

.mission-name {
  align-items: center;
  display: flex;
  flex-basis: 100%;
  font-family: Epilogue , serif;
  font-size: 19px;
  font-style: normal;
  font-weight: 300;
  line-height: 120%;
  text-transform: uppercase;
}

.mission-year {
  align-items: center;
  display: flex;
  font-family: Epilogue , serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
}

.mission-country {
  align-items: center;
  display: flex;
  font-family: Epilogue , serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
}

.mission-dest {
  align-items: center;
  display: flex;
  font-family: Epilogue , serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
}

.missions-container div:hover {
  background-color: rgba(255, 255, 255, 0.521);
  color: white;
  text-shadow: 2px 2px 2px black;
}
